import React from 'react';
import Button from '@material-ui/core/Button';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';

import {
    makeStyles,
    createMuiTheme,
    MuiThemeProvider
} from "@material-ui/core/styles";

const useStyles = makeStyles({
button: {
    textTransform: "none"
}
});
const defaultTheme = createMuiTheme();
const theme = createMuiTheme({
typography: {
    button: {
    textTransform: "none"
    }
}
});

const SelectBook = props => {
    const divStyle = { 
        display: props.displayModal ? 'block' : 'none'
    };
    function closeModal(e) {
        e.stopPropagation();
        props.closeModal();
    }

    const OLD_BOOKS = ["GEN","EXO","LEV","NUM","DEU","JOS","JDG","RUT","1SA","2SA","1KI","2KI","1CH","2CH","EZR","NEH","EST","JOB","PSA","PRO","ECC","SNG","ISA","JER","LAM","EZK","DAN","HOS","JOL","AMO","OBA","JON","MIC","NAM","HAB","ZEP","HAG","ZEC","MAL"];
    const OLD_NAMES = ["Genesis","Exodus","Leviticus","Numbers","Deuteronomy","Joshua","Judges","Ruth","1 Samuel","2 Samuel","1 Kings","2 Kings","1 Chronicles","2 Chronicles","Ezra","Nehemiah","Esther","Job","Psalms","Proverbs","Ecclesiastes","Song of Songs","Isaiah","Jeremiah","Lamentations","Ezekiel","Daniel","Hosea","Joel","Amos","Obadiah","Jonah","Micah","Nahum","Habakkuk","Zephaniah","Haggai","Zechariah","Malachi"];
    const NEW_BOOKS = ["MAT","MRK","LUK","JHN","ACT","ROM","1CO","2CO","GAL","EPH","PHP","COL","1TH","2TH","1TI","2TI","TIT","PHM","HEB","JAS","1PE","2PE","1JN","2JN","3JN","JUD","REV"];
    const NEW_NAMES = ["Matthew","Mark","Luke","John","Acts","Romans","1 Corinthians","2 Corinthians","Galatians","Ephesians","Philippians","Colossians","1 Thessalonians","2 Thessalonians","1 Timothy","2 Timothy","Titus","Philemon","Hebrews","James","1 Peter","2 Peter","1 John","2 John","3 John","Jude","Revelation"];

    function clickBook(index, isOld){
        console.log("clickBook : "+ index);
        if(isOld) 
            props.closeModal(OLD_BOOKS[index]);
        else
            props.closeModal(NEW_BOOKS[index]);
    }
    const classes = useStyles();

    return (
        <div className="selectBook" onClick={ closeModal } style={divStyle}>
        <div className="selectBook-content" onClick={ e => e.stopPropagation() }>
            <div><b>Old</b></div>
            <span className="close" onClick={closeModal} style={{position:'absolute', right:'20px',top:'55px'}}>&times;</span>
            <GridList cellHeight={40} cols = {2}>
            {OLD_BOOKS.map( (item,index) => 
                <GridListTile cols={1}>
                    <Button className={classes.button} onClick={() => clickBook(index,'true')}>{OLD_NAMES[index]}</Button>
                </GridListTile>
                )
            }
            </GridList>
            <div><b>New</b></div>
            <GridList cellHeight={40} cols = {2}>
            {NEW_BOOKS.map( (item,index) => 
                <GridListTile cols={1}>
                    <Button className={classes.button} onClick={() => clickBook(index,'false')}>{NEW_NAMES[index]}</Button>
                </GridListTile>
                )
            }
            </GridList>
        </div>
        </div>
    );
}

export default SelectBook;
